* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@media only screen and (max-width: 600px) {
  .nav-links h2.logo {
    font-size: 25px;
  }

  div.nav-links {
    padding: 5px;
    display: block;
    position: relative;
  }

  .links-container {
    display: none;
  }

  .show {
    display: block;
  }

  .nav-links nav ul {
    width: 100%;
    z-index: 1;
    height: 40vh;
