    color: #fff;
    background-color: #0000;
    border-radius: 0;
  }

  span.hambuger {
    z-index: 2;
    padding: 10px;
    transition: all .5s linear;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
  }

  span.hambuger:hover {
    transform: rotate(90deg);
  }

  div.house-of-nails {
    margin-top: 0;
  }

  div.house-of-nails h1 {
    max-width: 60%;
