    display: none;
  }

  div.welcome-article {
    max-width: 100%;
    height: auto;
    margin: auto;
    padding: 5px;
  }

  div.welcome-article h2 {
    font-size: 25px;
  }

  .img-div2 {
    display: none;
  }

  div.service-header {
    margin: 10px 0;
    font-size: 16px;
  }

  div.tab {
    width: 100%;
  }

  div.tab > button {
    margin: 0;
    padding: 12px 14px;
    font-size: 11px;
  }

  div.tab-content > div {
    width: 70%;
    height: 500px;
  }

  div.tab-content > div h3 {
    margin-top: 30px;
    padding: 0 5px;
  }

  body > section#technician {
    display: block;
